import {useEffect} from 'react';
import { useParams } from 'react-router-dom';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons'
import { useSelector, useDispatch } from 'react-redux';
import { selectClassProducts, getProductsByCategory } from '../../../redux/productReducer';
import { addCurOrders } from '../../../redux/orderReducer';
import { API_IMAGE_PATH } from '../../../config/constants';
import { selectCurrentGroup } from '../../../redux/orderReducer';
import { openSnackBar } from '../../../redux/snackBarReducer';

const Details = () => {
  const { categoryId } = useParams();
  const dispatch = useDispatch();
  const products = useSelector(selectClassProducts);
  const currentGroup = useSelector(selectCurrentGroup);

  useEffect(() => {
    dispatch(getProductsByCategory(categoryId));
  }, [categoryId]);

  const handleProduct = (p) => {
    if (currentGroup > 0) {
      dispatch(openSnackBar({ message: "Whiling checkout, cannot add product.", status: 'error' }));
      return;
    }
    const order = {
      productId: p._id,
      amount: 1
    }
    dispatch(addCurOrders(order));
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Point of Sale</p>
              </div>
              <div className="grid grid-cols-3 gap-6 px-[2rem] py-[2rem]">
                  {
                      products.map((product, index) => {
                          return (
                              <div 
                                  key={index} 
                                  className='w-full h-[9rem] flex flex-row text-xl rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'
                                  onClick={() => handleProduct(product)}
                              >
                                  <div className='w-[50%] flex flex-col text-center justify-center items-center'>
                                    <h1 className='font-bold'>{product.name}</h1>
                                    <p className='text-md'>{product.description}</p>                                  
                                    <p>Â£{product.price.toFixed(2)}</p>
                                  </div>                  
                                  <div className='w-[50%] flex flex-col text-center justify-center items-center p-[1rem]'>                
                                    <img 
                                      src={`${API_IMAGE_PATH}/${product.photo ? product.photo : "default.png"}`} 
                                      className='border border-gray-700 w-full h-full my-2 object-fill object-center'
                                    />
                                  </div>
                              </div>
                          )
                      })
                  }
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default Details;